<template>
  <form @submit.prevent="submitForm" name="sign in" class="col mx-auto">
    <b-form-group>
      <label for="email">Email:</label>
      <b-input
        type="email"
        class="input-field"
        v-model="email"
        ref="email"
        name="email"
        v-autofocus
      />
    </b-form-group>
    <b-form-group>
      <label for="password">Password:</label>
      <b-input
        type="password"
        name="password"
        v-model="password"
        ref="password"
      />
    </b-form-group>
    <b-form-group>
      <label for="repassword">Repeat password:</label>
      <b-input type="password" name="repassword" v-model="repassword"></b-input>
    </b-form-group>
    <b-button type="submit" ref="submit" variant="primary">
      Sign up
    </b-button>
  </form>
</template>

<script>
export default {
  data: () => ({
    email: '',
    password: '',
    repassword: ''
  }),
  methods: {
    async submitForm() {
      try {
        await this.$http.user.create({
          email: this.email,
          password: this.password,
          repassword: this.repassword
        })

        this.$router.push({ path: '/login' })
      } catch (error) {
        this.$alert.display(error)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
form {
  max-width: 300px;
  justify-self: center;
}
</style>
